.container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-image: url(https://t4.ftcdn.net/jpg/03/23/24/81/360_F_323248165_jEy8YPCTLfyJl6ApqDrnniTLdmM9aQP9.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}
input{
    width: 400px;
    height: 10px;
    border: none;
    padding: 20px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}
.search{
    margin: 0;
    padding: 15px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #c8c9cb;
}