.card{
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    width: 20%;
    height: 20rem;
    border: none;
    border-radius: 15px;
    transition: all 0.5s ease;
    box-shadow: 10px 10px 15px 5px rgba(0, 0, 0, 0.121);
}
.image{
    width: 70%;
    transition: all 0.5s ease;
    background-color: white;
}
.card:hover{
    width: 21%;
    cursor: pointer;
}
@media (max-width:800px){
    .card{
        width: 50%;
    }
    .card:hover{
        width: 51%;
    }
}