ul{
    background-color: rgba(240, 248, 255, 0);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
li{
    background-color: rgba(240, 248, 255, 0);
    list-style-type: none;
    margin: 0 10px;
    cursor: pointer;
}
h3{
    background-color: rgba(240, 248, 255, 0);
    padding: 0;
    margin: 0;
}